import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '@/components/HomePage.vue'
import AboutPage from '@/components/AboutPage.vue'
import EducationPage from '@/components/EducationPage.vue'
import MiscPage from '@/components/MiscPage.vue'
import PublicationsPage from '@/components/PublicationsPage.vue'
import TalkPage from '@/components/TalkPage.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/about',
    name: 'about',
    component: AboutPage
  },
  {
    path: '/education',
    name: 'education',
    component: EducationPage
  },
  {
    path: '/misc',
    name: 'misc',
    component: MiscPage
  },
  {
    path: '/publications',
    name: 'publications',
    component: PublicationsPage
  },
  {
    path: '/talk',
    name: 'talk',
    component: TalkPage
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;